.checkout-form {
  padding: 1rem 0;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
}

.checkout-form .form-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 1.05rem;
}

.form-control input {
  border-radius: 5px;
}

.checkout-actions {
  text-align: right;
}

.checkout-actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #8a2b06;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.checkout-actions button:hover,
.checkout-actions button:active {
  background-color: #5a1a01;
  border-color: #5a1a01;
  color: white;
}

.invalid input {
  background-color: rgb(255, 200, 200);
  border-color: red;
}
