.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  margin-bottom: 3.5rem;
  position: relative;
  background-color: #383838;
  color: white;
  border-radius: 14px;
  padding: 2rem 3rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  margin: 0 0 2rem 0;
}

.summary p {
  font-size: 1.15rem;
  margin-bottom: 1rem;
}
